import React, {useState} from 'react';
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";
import SuperCheckbox from "../h4/common/c3-SuperCheckbox/SuperCheckbox";
import s from "./Request.module.css"
import {RequestsAPI} from "./api";



const Request = () => {

    const [checkBoxStatus, setCheckBoxStatus] = useState<boolean>(false)
    const [response, setResponse] = useState<string>('Click button')

    const handler = (checkBoxStatus: boolean) => {
        RequestsAPI.postTest({body: {success: checkBoxStatus}})
            .then((d: any) => {
                console.log(d.data)
                setResponse(d.data.info)
            })
            .catch(error => {
                console.log({...error})
                console.log(error.response ? error.response.data.errorText : error.message)
                setResponse(error.response ? error.response.data.errorText : error.message)
            })
    }


    const checkBoxHandler = () => {
        setCheckBoxStatus(!checkBoxStatus)
    }

    return (<>
            <div className={s.mainBlock}>
                <div>
                    <SuperButton onClick={() => handler(checkBoxStatus)}>Кнопка</SuperButton>
                </div>
                <div>
                    <SuperCheckbox onChange={checkBoxHandler} className={s.checkbox}/>
                </div>
            </div>
            <div className={s.textResponse}>
                <b>Response from server:</b> {response}
            </div>
        </>
    );
};

export default Request;
